<template>
  <div style="padding: 10px">
    <div style="margin: 10px 0">
      <el-button type="primary" @click="add"></el-button>
    </div>

    <div style="margin: 10px 0">
      <el-input v-model="search" placeholder="" style="width: 20%" clearable></el-input>
      <el-button type="primary" style="margin-left: 5px" size="small" @click="load"></el-button>
    </div>

    <el-table
        :data="tableData"
        border
        stripe
        style="width: 100%"
        :row-class-name="tableRowClassName"
    >
      <el-table-column prop="id" label="ID" sortable/>
      <el-table-column prop="title" label="" />
<!--      <el-table-column prop="content" label="" />-->
      <el-table-column prop="author" label="" />
      <el-table-column prop="time" label="" />

      <el-table-column fixed="right" label="" width="120">
        <template #default="scope">
          <el-button link type="text" size="mini" @click="showDetail(scope.row)"></el-button>
          <el-button link type="text" size="mini" @click="handleEdit(scope.row)"></el-button>
          <el-popconfirm title="?" @confirm="handleDelete(scope.row.id)">
            <template #reference>
              <el-button type="text" size="mini"></el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-left: 10px">
      <el-pagination
          v-model:currentPage="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="pageSize"
          :small="small"
          :disabled="disabled"
          :background="background"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />

      <el-dialog
          v-model="dialogVisible"
          title=""
          width="60%"
          :before-close="handleClose"
      >
        <el-form :model="form" label-width="160px">

          <el-form-item label="">
            <el-input v-model="form.title" style="width: 50%" />
          </el-form-item>

          <div id="toolbar-container"></div>
          <div id="editor-container"></div>

<!--          <el-form-item label="">-->
<!--            <el-input v-model="form.content" style="width: 80%" />-->
<!--          </el-form-item>-->
        </el-form>

        <template #footer>
          <span class="dialog-footer">
            <el-button @click="dialogVisible = false"></el-button>
            <el-button type="primary" @click="save"></el-button>
          </span>
        </template>
      </el-dialog>

      <el-dialog
          v-model="vis"
          title=""
          width="60%"
          :before-close="handleClose"
      >
        <el-card>
          <div v-html="detail.content" style="min-height: 100px"></div>
        </el-card>
      </el-dialog>

    </div>
  </div>
</template>

<!--&lt;!&ndash;  css &ndash;&gt;-->
<!--<link href="https://unpkg.com/@wangeditor/editor@latest/dist/css/style.css" rel="stylesheet">-->


<script>

import request from "@/utils/request";
import '@wangeditor/editor/dist/css/style.css'
import { createEditor, createToolbar, IEditorConfig, IDomEditor } from '@wangeditor/editor'

let editor;
let editorConfig;
const E = window.wangEditor; // 

export default {
  name: 'News',
  components: {
  },
  data() {
    return {
      form: {},
      dialogVisible: false,
      search: '',
      currentPage: 1,
      pageSize: 10,
      total: 0,
      tableData: [],
      vis: false,
      detail: {}
    }
  },

  created() {
    this.load()
  },

  mounted() {

  },

  methods: {
    showDetail(row) {
      this.detail = row
      this.vis = true
    },

    filesUploadSuccess(res) {
      console.log(res)
      this.form.cover = res.data
    },

    load() {
      request.get("/news", {
        params: {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          search: this.search
        }
      }).then(res => {
        console.log(res)
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },

    add() {
      this.dialogVisible = true
      this.form = {}

      if (!editor) {
        this.$nextTick(() => {
          // 
          editorConfig = {MENU_CONF: {}}
          editorConfig.placeholder = ''
          editorConfig.onChange = (editor) => {
            // 
            console.log('content', editor.children)
            console.log('html', editor.getHtml())
          }
          // 
          const toolbarConfig = {}

          editorConfig.MENU_CONF['uploadImage'] = {
            server: 'http://localhost:9090/files/editor/upload',
            fieldName: 'file', //
          }

          //div,neweditor
          editor = createEditor({
            selector: '#editor-container',
            html: '<p></p>', //  editor.getHtml()  html 
            config: editorConfig,
            mode: 'default' //  'simple' 
          })
          const toolbar = createToolbar({
            editor,
            selector: '#toolbar-container',
            config: toolbarConfig,
            mode: 'default' //  'simple' 
          })
        })
      } else {
        this.$nextTick(() => {
          editor.setHtml('<p></p>')
          editorConfig.MENU_CONF['uploadImage'] = {
            server: 'http://localhost:9090/files/editor/upload',
            fieldName: 'file',
          }
        })
      }

    },

    save() {
      this.form.content = editor.getHtml()//

      if (this.form.id) { //update
        request.put("/news", this.form).then(res => {
          console.log(res)
          if (res.code === '0') {
            this.$message({
              type: "success",
              message: ""
            })
          } else {
            this.$message({
              type: "error",
              message: res.msg
            })
          }
          this.load() //
          this.dialogVisible = false //
        })
      }
      else { //add a new one
        let userStr = sessionStorage.getItem("user") || "{}"
        let user = JSON.parse(userStr)
        this.form.author = user.nickname

        request.post("/news/save", this.form).then(res => {
          console.log(res)
          if(res.code === '0') {
            this.$message({
              type: "success",
              message: ""
            })
          } else {
            this.$message({
              type: "error",
              message: res.msg
            })
          }
          this.load() //
          this.dialogVisible = false //
        })
      }
      request.post("/news", this.form).then(res => {
        console.log(res)
      })
    },

    handleEdit(row) {
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true

      if (!editor) {
        this.$nextTick(() => {
          // 
          const editorConfig = {}
          editorConfig.placeholder = ''
          editorConfig.onChange = (editor) => {
            // 
            console.log('content', editor.children)
            console.log('html', editor.getHtml())
          }
          // 
          const toolbarConfig = {}

          editorConfig.MENU_CONF['uploadImage'] = {
            server: 'http://localhost:9090/files/editor/upload',
            fieldName: 'file', //
          }

          editor = createEditor({
            selector: '#editor-container',
            html: '<p></p>', //  editor.getHtml()  html 
            config: editorConfig,
            mode: 'default' //  'simple' 
          })

          const toolbar = createToolbar({
            editor,
            selector: '#toolbar-container',
            config: toolbarConfig,
            mode: 'default' //  'simple' 
          })
        })
      } else {
        this.$nextTick(() => {
          editor.setHtml('<p></p>')
          editorConfig.MENU_CONF['uploadImage'] = {
            server: 'http://localhost:9090/files/editor/upload',
            fieldName: 'file',
          }
        })
      }

      editor.getHtml(row.content)
    },

    handleDelete(id) {
      console.log(id)
      request.delete("/news/" + id).then(res => {
        if(res.code === '0') {
          this.$message({
            type: "success",
            message: ""
          })
        } else {
          this.$message({
            type: "error",
            message: res.msg
          })
        }
      })
      this.load()
    },

    handleSizeChange(pageSize) { //
      this.pageSize = pageSize
      this.load()
    },

    handleCurrentChange(pageNum) { //
      this.currentPage = pageNum
      this.load()
    }

  }
}

</script>

